/*
==========================================================================
 Base
==========================================================================
*/
*{
    margin: 0;
    padding: 0;
    color: #12396f;
}
body{
    line-height: 1.3;
    font-family: sans-serif;
    font-size: 16px;
    text-align: center;
}
.container{
    max-width: 960px;
    margin: 0 auto;
    padding: 10px 0;
}
a{
    text-decoration: none;
}
/*
==========================================================================
 Filters
==========================================================================
*/
.container-filter{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 10px 0;
}
.container-filter:first-child{
    border-bottom: 1px solid #bdbdbd;
}
.container-filter>*{
    line-height: 35px;
    padding: 5px;
    border: none;
}
.container-filter>*:nth-child(n+2){
    border: 1px solid #bfbfbf;
    padding: 12px 20px 12px 20px;
}
.filter{
    background-color: #e2e2e2;
}
.filter-text{
    font-size: 16px;
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    background: url(data/img/search.png) no-repeat 98% 50%;
    background-color: white;
    line-height: 1.4;
}
.filter-input{
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    margin-left:22px;
    font-size: 16px;
    color: #1b3e6f;
    padding-right: 30px;
}
.filter-name{
    font-size: 16px;
    color: #1b3e6f;
    padding-right: 30px;
    margin-right: 0px 10px
}
/*
==========================================================================
 Articles
==========================================================================
*/
.articles-list{
    padding: 0;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    list-style-type: none;
}
.title-articles{
    font-size: 22px;
    line-height: 40px;
    color: #1b3e6f;
    font-weight: bold;
    text-transform: uppercase;
}
.article{
    display: -ms-flexbox;
    display: flex;
    margin: 20px 10px 0;
    -ms-flex-preferred-size: 31%;
        flex-basis: 31%;
    -ms-flex-positive: 1;
        flex-grow: 1;
    -ms-flex-negative: 1;
        flex-shrink: 1;
    -ms-flex-preferred-size: calc(30% - 7.5px);
        flex-basis: calc(30% - 7.5px);
    -ms-flex-direction: column;
        flex-direction: column;
    background-color: #e2e2e2;
    position: relative;
}
.article-image{
    -o-object-fit: cover;
       object-fit: cover;
    width: 100%;
    height: 100%;
}
.article-video{
    -o-object-fit: cover;
       object-fit: cover;
    width: 100%;
    height: 100%;
    cursor: pointer;
}
.play{
    position: absolute;
    top: 15%;
    left: calc(50% - 27px);
    cursor: pointer;
}
.article-name{
    font-size: 14px;
    font-weight: 600;
    color: #7eafd4;
    line-height: 2;
    text-transform: uppercase;
}
.article-title{
    font-size: 18px;
    color: #1b3e6f;
    line-height: 1.3;
    padding-bottom: 5px;
}
.article-content{
    font-size: 14px;
    color: #44484e;
}
.article-info{
    text-align: left;
    padding: 10px 20px 35px;
}
button{
    color: #013f86;
    border: 1px solid #003d83;
    background: rgb(255, 255, 255);
    width: 233px;
    height: 50px;
    cursor: pointer;
    margin: 40px;
    font-size: 18px;
}
.popup{
    position: fixed;
    width:100%;
    height:100%;
    z-index:1;
    background: rgba(0, 0, 0, 0.3);
    left:0;
    top:0;
}
.window {
    position: fixed;
    z-index: 2;
    margin: 0 auto;
    right: 0;
    left: 0;
    top: 10%;
    text-align: center;
}
/*
==========================================================================
 Mobile
==========================================================================
*/
@media screen  and (max-width: 1000px){
    .filter {
        padding: 0 20px;
    }
}

@media screen  and (max-width: 790px){
    .container-filter>*:nth-child(n+2) {
        padding: 10px
    }
    .filter-text, .filter-input, .filter-name, button {
        font-size: 90%
    }
    .title-articles {
        font-size: 18px
    }
    .article-name, .article-content {
        font-size: 80%
    }
    .article-title {
        font-size: 16px
    }
    button {
        width: 200px;
        height: 40px;
    }
}

@media screen and (max-width: 720px) {
    .article-info {
        padding: 10px 15px 15px
    }
    .play {
        top: 13%
    }
}

@media screen and (max-width: 700px){
    .filter {
        padding: 0 10px;
    }
    .filter .container {
        padding: 0
    }
}

@media screen and (max-width: 670px){
    .filter-text, .filter-input, .filter-name, button {
        font-size: 80%
    }
    .filter-name {
        padding-right: 15px;
    }
    .filter-input {
        margin-left: 10px
    }
    .title-articles {
        font-size: 16px
    }
    .article-name, .article-content {
        font-size: 75%
    }
    .article-title {
        font-size: 14px
    }
    .play {
        top: 11%
    }
    button {
        width: 150px;
        height: 30px;
    }
}

@media screen and (max-width: 590px){
    .play {
        top: 8%
    }
}

@media screen and (max-width: 575px) {
    .filter-name {
        padding-right: 5px;
    }
    .filter-input {
        margin-left: 5px
    }
    .container-filter:last-child {
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-align: start;
            align-items: flex-start;
    }
    .filter-input {
        width: 100%;
    }
    button {
        width: 120px;
        height: 25px;
    }
}

@media screen and (max-width: 542px){
    .article-name {
        line-height: 1.5;
    }
}

@media screen and (max-width: 500px) {
    .container-filter {
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-align: start;
            align-items: flex-start;
    }
    .filter-text {
        width: 95%;
    }
    .filter-input {
        width: 100%;
    }
    .articles-list {
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .play {
        top: 30%
    }
}

@media screen and (max-width: 410px) {
    .play {
        top: 25%
    }
}

@media screen and (max-width: 340px) {
    .play {
        top: 23%
    }
}




